export function reducer(state, action) {
  const donor = state.files;
  switch (action.type) {

    case "add":
      const curFiles = action.payload;
      let newFiles = [];
      for (let i = 0; i < curFiles.length; i++) {
        let img = newImage(curFiles[i]);
        newFiles.push(img);
      };
      return {
        ...state, files: donor.concat(newFiles), afterAddFilesMod: true
      };

    case "remove":
      donor.splice(action.payload, 1, null);
      return {
        ...state, files: donor
      };

    case "copy":
      const i = action.payload
      console.log(i)
      return {
        ...state, files: donor.concat([donor[i]])
      }

      case "updateOptions":
        donor[action.payload.index].options = action.payload.options;
        return {
          ...state, files: donor
        }

        case "changeCrop":
          donor[action.payload.index].options.crop = action.payload.crop;
          // donor[action.payload.index].options.crop.y = newcrop.y;
          // donor[action.payload.index].options.crop.width = newcrop.width;
          // donor[action.payload.index].options.crop.height = newcrop.height;
          return {
            ...state, files: donor
          }

          case "rotate":
            donor[action.payload.index].options.rotate = action.payload.r;
            donor[action.payload.index].options.crop = decodeAspect(
              donor[action.payload.index].options.cadr,
              donor[action.payload.index].options.crop,
              donor[action.payload.index].options.format,
              action.payload.r
            )
            return {
              ...state, files: donor
            }

            case "changeFormat":
              donor[action.payload.index].options.format = action.payload.format;
              const ro = donor[action.payload.index].options.rotate;
              donor[action.payload.index].options.crop = decodeAspect(
                donor[action.payload.index].options.cadr,
                donor[action.payload.index].options.crop,
                donor[action.payload.index].options.format,
                ro
              )
              return {
                ...state, files: donor
              }

              case "changePaper":
                donor[action.payload.index].options.paper = action.payload.format;
                return {
                  ...state,
                  files: donor
                }

                case "changeCadr":
                  donor[action.payload.index].options.cadr = action.payload.format;
                  donor[action.payload.index].options.crop = decodeAspect(
                    donor[action.payload.index].options.cadr,
                    donor[action.payload.index].options.crop,
                    donor[action.payload.index].options.format,
                    donor[action.payload.index].options.rotate
                  )
                  return {
                    ...state,
                    files: donor
                  }

                  case "changeCount":
                    donor[action.payload.index].options.count = action.payload.format;
                    return {
                      ...state,
                      files: donor
                    }

                    case "crop":
                      donor[action.payload].options.cropped.push(donor[action.payload].options.preCrop);
                      return {
                        ...state, files: donor
                      }

                      case "setChangingMod":
                        let f = state.files;
                        if (state.changeMod) {
                          f.forEach((el) => {
                            if (el)
                              el.options.changed = false;
                          })
                        } else {
                          f.forEach((el) => {
                            if (el)
                              el.options.changed = true;
                          })
                        }
                        return {
                          ...state, files: f, changeMod: !state.changeMod
                        };

                      case "massSetFilesOptions":
                        const formData = action.payload;
                        state.files.forEach((f) => {
                          if (f && f.options.changed) {
                            formData.forEach((el) => {
                              f.options[el.name] = el.value;
                            })
                            // Передавалась ли опция кадрирования
                            const isCadr = formData.findIndex((el) => {
                              return (el.name === 'cadr')
                            })
                            // Если не передавалась, снимаем опции у выбранных фото
                            if (isCadr < 0) {
                              f.options.cadr = false;
                            }
                            // Если в процессе добавление фото, снимаем выбор
                            if (state.afterAddFilesMod) {
                              f.options.changed = false;
                            }
                          }
                        })
                        if (state.afterAddFilesMod) {
                          state.afterAddFilesMod = !state.afterAddFilesMod;
                        }
                        return {
                          ...state, files: state.files
                        }

                        default:
                          throw new Error();
  }
}

function newImage(file) {
  return {
    id: ID(file),
    file: file,
    body: URL.createObjectURL(file),
    options: {
      count: 1,
      format: '10x15',
      paper: 'матовая',
      cadr: false,
      rotate: false,
      changed: true,
      cropped: [],
      aspect: 10 / 15,
      crop: {},
    }
  };
}

function ID(file) {
  return file.size + '_' + file.name;
}

function correctCrop(crop) {
  let {
    unit,
    aspect,
    width,
    height,
    x,
    y
  } = {
    ...crop
  }

  if (y < 0) {
    height = height - y;
    width = height * aspect;
    x = 0;
    y = 0;
  }
  return {
    unit: unit,
    aspect: aspect,
    width,
    height,
    x,
    y
  }
}

const decodeAspect = (cd, c, f, r) => {
  const a = f.split('x');
  c.aspect = a[0] / a[1];
  if (cd) { // Кадрирование вкл
    if (r) { // Рамка повёрнута горизонтально
      c.aspect = a[1] / a[0];
      if (c.naturalWidth < c.naturalHeight) { // Изображение вертикальное
        c.height = c.naturalHeight;
        c.y = 0;
        c.width = c.naturalHeight * c.aspect;
        c.x = (c.naturalWidth - c.width) / 2;
      } else { // Изображение горизонтальное
        c.width = c.naturalWidth;
        c.x = 0;
        c.height = c.naturalWidth * c.aspect;
        c.y = (c.naturalHeight - c.height) / 2;
      }
    } else { // Рамка повёрнута вертикально
      if (c.naturalWidth < c.naturalHeight) { // Изображение вертикальное
        c.height = c.naturalHeight;
        c.y = 0;
        c.width = c.naturalHeight * c.aspect;
        c.x = (c.naturalWidth - c.width) / 2;

        if (c.width < c.naturalWidth) { // Если ширина оказалась меньше
          c.width = c.naturalWidth;
          c.x = 0;
          c.height = c.naturalWidth / c.aspect;
          c.y = (c.naturalHeight - c.height) / 2;
        }
      } else { // Изображение горизонтальное
        c.width = c.naturalWidth;
        c.x = 0;
        c.height = c.naturalWidth * c.aspect;
        c.y = (c.naturalHeight - c.height) / 2;
      }
    }
  } else { // Кадрирование выкл
    if (r) { // Рамка повёрнута горизонтально
      c.aspect = a[1] / a[0];
      if (c.naturalWidth < c.naturalHeight) { // Изображение вертикальное
        c.width = c.naturalWidth;
        c.x = 0;
        c.height = c.naturalWidth / c.aspect;
        c.y = (c.naturalHeight - c.height) / 2;
      } else { // Изображение горизонтальное
        c.width = c.naturalWidth;
        c.x = 0;
        c.height = c.naturalWidth / c.aspect;
        c.y = (c.naturalHeight - c.height) / 2;

        if (c.height > c.naturalHeight) {
          c.height = c.naturalHeight;
          c.y = 0;
          c.width = c.naturalHeight * c.aspect;
          c.x = (c.naturalWidth - c.width) / 2;
        }
      }
    } else { // Рамка повёрнута вертикально
      if (c.naturalWidth < c.naturalHeight) { // Изображение вертикальное
        c.height = c.naturalHeight;
        c.y = 0;
        c.width = c.naturalHeight * c.aspect;
        c.x = (c.naturalWidth - c.width) / 2;
      } else { // Изображение горизонтальное
        c.width = c.naturalWidth;
        c.x = 0;
        c.height = c.naturalWidth * c.aspect;
        c.y = (c.naturalHeight - c.height) / 2;

        if (c.height > c.naturalHeight) {
          c.height = c.naturalHeight;
          c.y = 0;
          c.width = c.naturalHeight * c.aspect;
          c.x = (c.naturalWidth - c.width) / 2;
        }
      }
    }
  }

  return c;
}